import React from "react";
import "./Actor.css";

const Actor = ({ image, charName, actorName, actorId }) => {
    return (
        <div className="actor-card">
            {/*IF in image check if API call returns null or images ID*/}
            <img
                src={
                    image === "https://image.tmdb.org/t/p/w500/null"
                        ? "data:image/png;base64,"
                        : image
                }
                alt={actorId}
            />
            <div className="actor-info">
                <h5>{actorName}</h5>
                <h4>{charName}</h4>
            </div>
        </div>
    );
};

export default Actor;
